import { Component, OnDestroy, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { Space } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';
import {
    ConfirmModalComponent,
    ConfirmModalData,
    CONFIRM_METADATA,
} from '../confirm-modal/confirm-modal.component';
import { TimeExtensionModalComponent } from '../time-extension/time-extension.component';
import {
    RequirementDetailsModalComponent,
    RequirementDetailsModalData,
} from 'src/app/shell/bookings/overlays/requirement-details-modal/requirement-details-modal.component';
import { generateBookingForm } from 'src/app/services/data/bookings/booking.utilities';

import * as dayjs from 'dayjs';
import { BookingsService } from 'src/app/services/data/bookings/bookings.service';
import { UsersService } from 'src/app/services/data/users/users.service';

export interface MeetingDetailsModalData {
    /** Meeting to display on the modal */
    booking: Booking;
    /** Whether local user is able act on behalf of the host */
    as_delegate: boolean;
    /** Email address of the associated delegate */
    delegate: string;
    /** Whether booking is the first in the list */
    is_first: boolean;
    /** Whether booking is the last in the list */
    is_last: boolean;
}

/*istanbul ignore next */
@Component({
    selector: 'meeting-details-overlay',
    templateUrl: './meeting-details-overlay.template.html',
    styleUrls: ['./meeting-details-overlay.styles.scss'],
})
export class MeetingDetailsOverlayComponent extends BaseDirective implements OnDestroy {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Display string of the loading state of the meeting */
    public loading: string;
    /** Whether booking is being edited */
    public edit: boolean;

    /** Active booking to display */
    public get booking(): Booking {
        return this._data.booking;
    }

    /** Whether booking is the first in the list */
    public get is_last(): boolean {
        return this._data.is_last;
    }

    /** Whether booking is the last in the list */
    public get is_first(): boolean {
        return this._data.is_first;
    }

    /** List of spaces associated with the event */
    public get space_list(): Space[] {
        return this.booking.space_list || (this.booking.space ? [this.booking.space] : []);
    }

    public get has_recurrence(): boolean {
        return !!(this.booking.recurrence && this.booking.recurrence.period);
    }

    /** Whether user is able to edit the booking */
    public get can_edit(): boolean {
        const user = this._users.current;
        return (
            this.booking.organiser.email === user.email ||
            (this._data.as_delegate && user.delegates.includes(this.booking.organiser.email))
        );
    }

    /** Whether booking is in progress */
    public get in_progress(): boolean {
        if (!this.booking) {
            return false;
        }
        const now = dayjs();
        const start = dayjs(this.booking.date);
        const end = start.add(this.booking.duration, 'm');
        return now.isAfter(start, 's') && now.isBefore(end, 'm');
    }

    /** Whether booking has ended */
    public get has_ended(): boolean {
        if (!this.booking) {
            return false;
        }
        const now = dayjs();
        const start = dayjs(this.booking.date);
        const end = start.add(this.booking.duration, 'm');
        return now.isAfter(end, 's');
    }

    /** Whether booking has a space associated with it */
    public get has_space(): boolean {
        if (!this.space_list || !this.space_list.length) {
            return false;
        }
        for (const space of this.space_list) {
            if (space) {
                return true;
            }
        }
        return false;
    }

    constructor(
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<MeetingDetailsOverlayComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: MeetingDetailsModalData,
        private _service: ApplicationService,
        private _users: UsersService,
        private _bookings: BookingsService,
        private _router: Router
    ) {
        super();
    }

    /**
     * Show the next meeting in the list
     */
    public nextBooking() {
        this.event.emit({ reason: 'action', metadata: 'next' });
    }

    /** Show the previous meeting in the list */
    public previousBooking() {
        this.event.emit({ reason: 'action', metadata: 'previous' });
    }

    /** Open confirmation modal for deleting the meeting */
    public confirmDeleteMeeting(): void {
        const can_delete =
            this._data.as_delegate || this.booking.organiser.email === this._users.current.email;
        const ref = this._dialog.open<ConfirmModalComponent, ConfirmModalData>(
            ConfirmModalComponent,
            {
                ...CONFIRM_METADATA,
                data: {
                    title: `${can_delete ? 'Delete' : 'Decline'} meeting`,
                    icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                    content: `
                        <p>Are you sure you want to ${can_delete ? 'delete' : 'decline'} this meeting on ${
                            this.booking.date_string
                        }${this.has_recurrence ? ' from your series' : ''}?</p>
                        <p>All attendees will be notified.</p>
                    `,
                },
            }
        );
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe((event) => {
                if (event.reason === 'done') {
                    this.deleteMeeting();
                    ref.close();
                    this.unsub('confirm');
                }
            })
        );
    }

    /** Open confirmation modal for deleting the series of bookings */
    public confirmDeleteSeries(): void {
        if (!this.booking.recurrence) {
            return;
        }
        const can_delete =
            this._data.as_delegate || this.booking.organiser.email === this._users.current.email;
        const ref = this._dialog.open<ConfirmModalComponent, ConfirmModalData>(
            ConfirmModalComponent,
            {
                ...CONFIRM_METADATA,
                data: {
                    title: `${can_delete ? 'Delete' : 'Decline'} series`,
                    icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                    content: `
                        <p>All bookings in the series will be ${can_delete ? 'deleted' : 'declined'}. Are you sure you want to ${can_delete ? 'delete' : 'decline'} the series?</p>
                        <p>All attendees will be notified.</p>
                    `,
                },
            }
        );
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe((event) => {
                if (event.reason === 'done') {
                    this.deleteSeries();
                    ref.close();
                    this.unsub('confirm');
                }
            })
        );
    }

    /** Navigate the booking with the details of the current booking set */
    public duplicateMeeting(): void {
        const new_booking = new Booking({ ...this.booking.toJSON(), id: 'ad-hoc', icaluid: '' });
        this._router.navigate(['/book']);
        if (localStorage) {
            localStorage.setItem('ACA_STAFF.booking_form_data', JSON.stringify(new_booking));
        }
        this._dialog_ref.close();
    }

    /** Open modal to extend the length of the meeting */
    public extendMeeting() {
        this.loading = 'Extending meeting...';
        const ref = this._dialog.open(TimeExtensionModalComponent, {
            ...CONFIRM_METADATA,
            data: { booking: this.booking },
        });
        ref.afterClosed().subscribe(() => (this.loading = null));
    }

    public editMeetingRequirements() {
        const form = generateBookingForm(this.booking, [
            'space_list',
            'notes',
            'equipment_codes',
            'expected_attendees',
        ] as any);
        const ref = this._dialog.open<
            RequirementDetailsModalComponent,
            RequirementDetailsModalData
        >(RequirementDetailsModalComponent, {
            maxWidth: '95vw',
            width: '32em',
            data: {
                spaces: form.controls.space_list.value,
                notes_field: form.controls.notes,
                codes_field: form.controls.equipment_codes,
                head_counts_field: form.controls.expected_attendees,
            },
        });
        this.subscription(
            'modal_events',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                if (event.reason === 'done') {
                    this.loading = 'Updating space requirements...';
                    const updated_booking = new Booking({
                        ...this.booking,
                        ...form.value,
                        room_ids: form.value.space_list.map((space) => space.id),
                    });
                    updated_booking.save().then(
                        (new_booking: Booking) => {
                            this._data.booking = new_booking;
                            this.loading = null;
                        },
                        (err) => {
                            this.loading = null;
                            this._service.notifyError(
                                `Error updating space requirements. Error: ${err.message || err}`
                            );
                        }
                    );
                    ref.close();
                }
            })
        );
        ref.afterClosed().subscribe(() => this.unsub('modal_events'));
    }

    /**
     * Open booking form to catering section
     */
    public editMeetingCatering() {
        this.editMeeting(false);
        this._router.navigate(['/book', 'space', 'catering']);
        this._dialog_ref.close();
    }

    /**
     * Open booking form to edit details
     */
    public editMeeting(navigate: boolean = true) {
        if (localStorage) {
            localStorage.setItem('ACA_STAFF.booking_form_data', JSON.stringify(this.booking));
        }
        if (navigate) {
            this._router.navigate(['/book']);
            this._dialog_ref.close();
        }
    }

    /** Delete the meeting */
    private deleteMeeting() {
        const can_delete =
            this._data.as_delegate || this.booking.organiser.email === this._users.current.email;
            this.loading = `${can_delete ? 'Deleting' : 'Declining'} meeting...`;
        const params: any = [this.booking.id, {
            icaluid: this.booking.icaluid,
            host_email: this.booking.organiser.email,
            user_email: this._data.as_delegate ? this._data.delegate : this._users.current.email,
            room_id: this.booking.space?.id,
            start: Math.floor(this.booking.date / 1000),
        }];
        const method = can_delete ? this._bookings.delete(params[0], params[1]) : this._bookings.decline(params[0], params[1]);
        method.then(
            () => {
                this._service.notifySuccess(`Successfully ${can_delete ? 'deleted' : 'declined'} meeting.`);
                this.event.emit({ reason: 'action' });
                this.loading = null;
                this._dialog_ref.close();
            },
            (err) => {
                this.loading = null;
                this._service.notifyError(`Error ${can_delete ? 'deleting' : 'declining'} meeting. Error: ${err.message || err}`);
            }
        );
    }

    /** Delete series */
    private deleteSeries() {
        const can_delete =
            this._data.as_delegate || this.booking.organiser.email === this._users.current.email;
        this.loading = `${can_delete ? 'Deleting' : 'Declining'} series...`;
        const params: any = [this.booking.recurrence.series_id, {
            icaluid: this.booking.icaluid,
            host_email: this.booking.organiser.email,
            user_email: this._data.as_delegate ? this._data.delegate : this._users.current.email,
            room_id: this.booking.space?.id,
            start: Math.floor(this.booking.date / 1000),
        }];
        const method = can_delete ? this._bookings.delete(params[0], params[1]) : this._bookings.decline(params[0], params[1]);
        method.then(
            () => {
                this._service.notifySuccess(`Successfully ${can_delete ? 'deleted' : 'declined'} series.`);
                this.event.emit({ reason: 'action' });
                this.loading = null;
                this._dialog_ref.close();
            },
            (err) => {
                this.loading = null;
                this._service.notifyError(`Error ${can_delete ? 'deleting' : 'declining'} series. Error: ${err.message || err}`);
            }
        );
    }
}
