import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { catchError, debounceTime, distinctUntilChanged, first, map, switchMap, take } from 'rxjs/operators';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';

import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { ISpaceAvailabilityOptions, Space } from 'src/app/services/data/spaces/space.class';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { randomInt } from 'src/app/shared/utilities/general.utilities';
import { sort } from 'src/app/services/data/spaces/space.utilities';
import { BookingFlowEvent } from '../../bookings.component';

import * as dayjs from 'dayjs';
import { AlertMode } from '../../../../shared/components/alert-banner/alert-banner.component';
import { Urls } from '../../../../shared/utilities/urls';

export enum BannerMode {
    None,
    OfficeRules,
    SearchFilters
}

@Component({
    selector: 'booking-find-space',
    templateUrl: './find-space.component.html',
    styleUrls: ['./find-space.component.scss'],
})
export class BookingFindSpaceComponent extends BaseDirective implements OnInit, OnChanges {
    public AlertMode = AlertMode;

    /** Form control for setting the active space */
    @Input() public spaces: FormControl;
    /** Form group for booking */
    @Input() public form: FormGroup;
    /** Emitter for flow update events */
    @Output() public event = new EventEmitter<BookingFlowEvent>();

    /** List of spaces available during the required period */
    public space_list: Space[] = [];
    /** List of users from an API search */
    public search_results$: Observable<Space[]>;
    /** Whether space list is loading */
    public loading: boolean;
    /** List of zones to filter the spaces on */
    public zone_ids: string[] = [];
    /** Subject holding the value of the search */
    public readonly change$ = new Subject<string>();

    public hasLoaded = false;
    public bannerMode = new BehaviorSubject<BannerMode>(BannerMode.None);

    public get showBanner(): Observable<boolean> {
        return this.bannerMode.pipe(map(value => value !== BannerMode.None));
    }

    public get bannerMessage(): Observable<string> {
        return this.bannerMode.pipe(map(value => {
            switch (value) {
                case BannerMode.OfficeRules:
                    return 'No rooms available due to local office rules applied. ' +
                        'Please, contact local admin for more information.';
                case BannerMode.SearchFilters:
                    return 'There are no available rooms for the selected date and/or time.';
                case BannerMode.None:
                    return '';
            }
        }));
    }

    public get bannerButtonText(): Observable<string> {
        return this.bannerMode.pipe(map(value => {
            switch (value) {
                case BannerMode.OfficeRules:
                    return 'Show Office Contacts';
                case BannerMode.SearchFilters:
                    return 'Return to Search';
                case BannerMode.None:
                    return '';
            }
        }));
    }

    /**  Whether multiple spaces are allowed to be selected */
    public get multiple(): boolean {
        return this._service.setting('app.booking.multiple_spaces');
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _org: OrganisationService,
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            let request_id = 0;
            // Listen for input changes
            this.search_results$ = this.change$.pipe(
                debounceTime(400),
                distinctUntilChanged(),
                switchMap((_) => {
                    this.loading = true;
                    request_id = randomInt(9999_9999);
                    const recurrence = this.form.controls.recurrence
                        ? this.form.controls.recurrence.value
                        : null;
                    const recurrence_properties =
                        recurrence?.period && recurrence.period !== 'None'
                            ? {
                                recurr_period: (recurrence.period || '').toLowerCase(),
                                recurr_end: dayjs(recurrence.end * 1000)
                                    .endOf('d')
                                    .unix(),
                            }
                            : {};
                    const date = dayjs(this.form.controls.date.value);
                    const query: ISpaceAvailabilityOptions = {
                        date: this.form.controls.all_day.value
                            ? date.startOf('d').valueOf()
                            : date.valueOf(),
                        duration: this.form.controls.all_day.value
                            ? 24 * 60
                            : this.form.controls.duration.value,
                        zone_ids: this._org.building.id,
                        bookable: true,
                        ...recurrence_properties,
                    };
                    /* istanbul ignore else */
                    if (this.zone_ids && this.zone_ids.length) {
                        query.zone_ids = this.zone_ids.join(',');
                    }
                    const id = request_id;
                    return this._spaces.available(query).then(async (list) => ({ id, list }));
                }),
                catchError((_) => of({ id: request_id, list: [] })),
                map((resp: { id: number; list: Space[] }) => {
                    this.loading = false;
                    this.hasLoaded = true;
                    const list = resp.id === request_id ? resp.list : this.space_list;
                    const recurrence = this.form.controls.recurrence
                        ? this.form.controls.recurrence.value
                        : null;
                    const date = dayjs(this.form.controls.date.value);
                    return recurrence?.period && recurrence.period !== 'None'
                        ? list.filter((space) =>
                            space.recurr_available.find(
                                (block) =>
                                    block.available && dayjs(block.date * 1000).isSame(date, 'd'),
                            ),
                        )
                        : list;
                }),
            );
            // Process API results
            this.subscription(
                'search_results',
                this.search_results$.subscribe((list) => {
                    // count if a booking was filtered out for rule, or availability
                    // we will change the mode to match whichever is largest.
                    let rulesCount = 0;
                    let availableCount = 0;
                    this.space_list = list.filter((space) => {
                        const rules = space.rulesFor({
                            date: this.form.controls.date.value,
                            duration: this.form.controls.all_day.value
                                ? 24 * 60
                                : this.form.controls.duration.value,
                            host: this.form.controls.organiser.value,
                        });
                        const hide = rules.hide;
                        const spaceNotAvailable = !space.was_available;
                        if (hide || spaceNotAvailable) {
                            rulesCount += hide ? 1 : 0;
                            availableCount += spaceNotAvailable ? 1 : 0;
                            return false;
                        }
                        for (const zone of this.zone_ids) {
                            if (space.zones.includes(zone)) {
                                return true;
                            }
                        }
                        return !this.zone_ids.length;
                    });
                    this.space_list.sort((a, b) => sort(a, b, this._org.buildings));
                    if (rulesCount > 0 && !this.space_list.length) {
                        this.bannerMode.next(BannerMode.OfficeRules);
                    } else if (availableCount > 0 && !this.space_list.length) {
                        this.bannerMode.next(BannerMode.SearchFilters);
                    } else {
                        this.bannerMode.next(BannerMode.None);
                    }
                }),
            );
            this.change$.next('');
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.form) {
            const onChange = (_) => this.change$.next(_);
            /* istanbul ignore else */
            if (this.form.controls.date) {
                this.subscription(
                    'date_field',
                    this.form.controls.date.valueChanges.subscribe(onChange),
                );
            }
            /* istanbul ignore else */
            if (this.form.controls.duration) {
                this.subscription(
                    'duration_field',
                    this.form.controls.duration.valueChanges.subscribe(onChange),
                );
            }
        }
    }

    /**
     * Update the selected space list
     * @param space Space to add/toggle in the selected list
     */
    public selectSpace(space: Space) {
        if (this.multiple) {
            const list = this.spaces.value;
            if (list.find((item) => item.id === space.id)) {
                this.spaces.setValue(list.filter((item) => item.id !== space.id));
            } else {
                this.spaces.setValue(list.concat([space]));
            }
        } else {
            this.spaces.setValue([space]);
            this.event.emit({ type: 'next', step: 'search' });
        }
    }

    /**
     * Proceed to the next step in the space booking flow
     */
    public next(): void {
        this.event.emit({ type: 'next', step: 'search' });
    }

    /** Move flow to previous step */
    public previous(): void {
        this.event.emit({ type: 'previous', step: 'search' });
    }

    public closeBanner() {
        this.bannerMode.next(BannerMode.None);
    }

    public bannerActionButtonClick() {
        this.bannerMode.pipe(take(1)).subscribe(value => {
            switch (value) {
                case BannerMode.OfficeRules:
                    window.open(Urls.OfficeContacts);
                    break;
                case BannerMode.SearchFilters:
                    this.previous();
                    break;
            }
        });
    }
}
