import { Component, OnInit } from '@angular/core';
import { first } from 'rxjs/operators';

import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationLink, HomePageSettings } from 'src/app/shared/utilities/settings.interfaces';
import { AlertMode } from '../../shared/components/alert-banner/alert-banner.component';
import { Urls } from '../../shared/utilities/urls';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss'],
})
export class HomeComponent extends BaseDirective implements OnInit {
    /** Settings for the home page */
    public settings: HomePageSettings;
    /** Whether to show the overlay menu */
    public show_menu: boolean;

    /**
     * Whether to show alert banner.
     */
    public showBanner = true;

    public AlertMode = AlertMode;

    /** Tiles to display on the home page */
    public get tiles(): ApplicationLink[] {
        return (this.settings || { tiles: [] }).tiles || [];
    }

    /** Min width of a tile */
    public get tile_width(): number {
        const tiles = this.tiles;
        if (tiles.length > 2) {
            return 49;
        }
        return 99;
    }

    /** Height of each tile */
    public get tile_height(): number {
        const tiles = this.tiles;
        if (tiles.length > 2) {
            return 100 / ((tiles.length + tiles.length % 2) / 2);
        }
        return 50;
    }

    constructor(private _service: ApplicationService) {
        super();
    }

    public ngOnInit(): void {
        this._service.title = 'Home';
        this._service.initialised.pipe(first(_ => _)).subscribe(() => {
            this.settings = this._service.setting('app.home') || {};
            localStorage.removeItem('ACA_STAFF.booking_form_data');
        });
    }

    public hideBanner() {
        this.showBanner = false;
    }

    public learnMore() {
        window.open(Urls.LearnMore);
    }
}
