import { Component, Input, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { BaseTemplateComponent } from '../base-template.component';

@Component(
    {
        selector: 'app-informational-image',
        template: `
            <ng-template>
                <div class="body">
                    <div class="info-block">
                        <div class="image-wrapper">
                            <img src="{{src}}">
                        </div>
                    </div>
                </div>
            </ng-template>
        `,
        styleUrls: ['./informational-image.component.scss'],
        encapsulation: ViewEncapsulation.None,
    },
)
export class InformationalImageComponent extends BaseTemplateComponent {

    @Input()
    public src: string;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }
}
