import { BaseDataClass } from '../base-api.class';

import { BookingNote, BookingRecurrenceDetails } from './booking.interfaces';
import { CateringOrder } from '../catering/catering-order.class';
import { User } from '../users/user.class';
import { HashMap } from '../../../shared/utilities/types.utilities';
import { humaniseDuration, unique, flatten } from '../../../shared/utilities/general.utilities';
import { Space } from '../spaces/space.class';
import { ServiceManager } from '../service-manager.class';

import * as dayjs from 'dayjs';

export class Booking extends BaseDataClass {
    /** Unique calendar event ID */
    public readonly icaluid: string;
    /** Subject or title of the booking */
    public readonly title: string;
    /** Unix timestamp of the booking start */
    public readonly date: number;
    /** Length of the booking in minutes */
    public readonly duration: number;
    /** Description or details of the booking */
    public readonly body: string;
    /** Type of booking */
    public readonly class: string;
    /** Whether the booking has catering associated with it */
    public readonly has_catering: boolean;
    /** List of catering orders for the booking */
    public readonly catering: readonly CateringOrder[];
    /** String location set in booking */
    public readonly location_name: string;
    /** Whether booking's duration covers all day */
    public readonly all_day: boolean;
    /** Details of the recurrence */
    public readonly recurrence: BookingRecurrenceDetails;
    /** Mapping of emails to approval statuses */
    public readonly approval_status: HashMap<string>;
    /** Host/Organiser of the booking */
    public readonly organiser: User;
    /** List of people invited to attend the booking */
    public readonly attendees: User[];
    /** Author of the booking */
    public readonly creator: User;
    /** List of notes associated with the booking */
    public readonly notes: readonly BookingNote[];
    /** Mapping of spaces to equipment charge codes */
    public readonly equipment_codes: HashMap<string>;
    /** Mapping of spaces to expected number of attendees */
    public readonly expected_attendees: HashMap<number>;
    /** Setup time before the meeting in minutes */
    public readonly setup: HashMap<number>;
    /** List of checked in attendees */
    public readonly checked_in: string[];
    /** Breakdown time after booking in minutes */
    public readonly breakdown: HashMap<number>;
    /** List of spaces associated with the booking */
    public readonly space_list: Space[];
    /** List of users to be notified on visitor arrivals */
    public readonly notify_users: readonly string[];
    /** Whether the time or duration has changed */
    public time_changed = false;
    /** Map of display strings */
    private _display: HashMap<string> = {};
    /** Service for managing Bookings */
    protected get _service() {
        return ServiceManager.serviceFor(Booking);
    }

    constructor(raw_data: HashMap = {}) {
        super(raw_data);
        this.icaluid = raw_data.icaluid || this.id || '';
        this.title = raw_data.title || raw_data.subject || raw_data.Subject || '';
        const time =
            raw_data.date || raw_data.start_epoch * 1000 || raw_data.start * 1000 || raw_data.Start;
        const start = dayjs(time).startOf('m');
        this.date = !time
            ? start.minute(Math.ceil(start.minute() / 5) * 5).valueOf()
            : start.valueOf();
        this.duration =
            raw_data.duration ||
            dayjs(raw_data.end_epoch * 1000 || raw_data.end * 1000 || raw_data.End).diff(
                start,
                'm'
            ) ||
            60;
        this.all_day = !!raw_data.all_day || this.duration > 23 * 60;
        if (this.all_day) {
            this.date = dayjs(this.date).startOf('d').valueOf();
        }
        this.body =
            (typeof raw_data.body === 'string' ? raw_data.body : '') || raw_data.description;
        this.class =
            raw_data.booking_type ||
            raw_data.class ||
            (raw_data.visitors ? 'external' : null) ||
            'internal';
        this.attendees = (raw_data.attendees || raw_data._attendees || []).map((i) => new User(i));
        this.organiser =
            (raw_data.organizer || raw_data.organiser
                ? new User(raw_data.organizer || raw_data.organiser)
                : User.active_user) || new User();
        this.creator =
            raw_data.booked_by || raw_data.creator
                ? new User(raw_data.booked_by || raw_data.creator)
                : this.organiser;
        this.location_name = raw_data.location_name || '';
        this.setup = raw_data.setup || 0;
        this.breakdown = raw_data.breakdown || 0;
        this.recurrence = raw_data.recurrence || raw_data.recurr || {};
        if (this.recurrence.end && this.recurrence.end < new Date().getTime()) {
            (this as any).recurrence = {
                ...this.recurrence,
                start: this.recurrence.start,
                end: this.recurrence.end,
            };
        }
        this.recurrence = {
            ...this.recurrence,
            series_id: raw_data.seriesMasterId,
        };
        this.notes = raw_data.notes || [];
        this.equipment_codes = raw_data.equipment_codes || raw_data.equipment_code || {};
        this.expected_attendees = raw_data.expected_attendees || {};
        this.approval_status = raw_data.approval_status || raw_data.status || {};
        this.checked_in = raw_data.checked_in || raw_data.checkins || [];
        this.notify_users = raw_data.notify_users || [];
        if (raw_data.description && !this.notes.find((i) => i.type === 'description')) {
            this.notes = [
                ...this.notes,
                {
                    type: 'description',
                    date: -1,
                    message: raw_data.description,
                    author: this.organiser.email,
                },
            ];
        }
        this._display = {
            date: start.format('DD MMM YYYY'),
            time: `${start.format('h:mma')} - ${dayjs(start)
                .add(this.duration, 'm')
                .format('h:mma')}`,
            start: start.format('h:mma'),
            end: dayjs(start).add(this.duration, 'm').format('h:mma'),
            duration: humaniseDuration(this.duration),
        };
        const space_ids = unique(
            raw_data.room_ids || raw_data.room_id || raw_data._space_list || []
        );
        const space_service = ServiceManager.serviceFor(Space);
        if (space_service) {
            this.space_list = space_ids.map((id) => {
                return space_service.find(id) || new Space({ id, name: id, email: id });
            });
        } else {
            this.space_list = space_ids.map((id) => new Space({ id, name: id, email: id }));
        }
        if (raw_data.show_as && raw_data.show_as === 'cancelled') {
            this.space_list.forEach((space) => (this.approval_status[space.email] = 'declined'));
        }
        this.catering = (raw_data.catering instanceof Array ? raw_data.catering : []).map(
            (i) => new CateringOrder(i)
        );
        this.has_catering = !!(raw_data.has_catering || (this.catering && this.catering.length));
    }

    /** Whether booking has been declined */
    public get declined(): boolean {
        if (this.location_name.toLowerCase().includes('decline')) {
            return true;
        }
        for (const space of this.space_list) {
            if ((this.approval_status[space.email] || '').indexOf('decline') >= 0) {
                return true;
            }
        }
        return false;
    }

    /** Whether booking has been approved */
    public get approved(): boolean {
        return !this.declined && !this.tentative;
    }

    /** Whether booking is tentative */
    public get tentative(): boolean {
        for (const space of this.space_list) {
            if (
                this.approval_status[space.email] &&
                this.approval_status[space.email].indexOf('tentative') >= 0
            ) {
                return true;
            }
        }
        return false;
    }

    /** Whether booking contains external visitors in the attendee list */
    public get has_visitors(): boolean {
        return this.attendees.reduce((a, v) => a || v.external, false);
    }

    /** Status of the booking */
    public get status(): 'future' | 'upcoming' | 'done' | 'started' | 'in_progress' {
        const now = dayjs();
        const date = dayjs(this.date);
        if (now.isBefore(date.subtract(15, 'm'), 'm')) {
            return 'future';
        } else if (now.isBefore(date, 'm')) {
            return 'upcoming';
        } else if (now.isBefore(date.add(15, 'm'), 'm')) {
            return 'started';
        } else if (now.isBefore(date.add(this.duration, 'm'), 'm')) {
            return 'in_progress';
        }
        return 'done';
    }

    /** Description of the booking purpose */
    public get description(): string {
        const note = (this.notes || []).find((i) => i.type === 'description');
        return note ? note.message : '';
    }

    /** Get the first space from the space list */
    public get space(): Space {
        return this.space_list[0] || ({ level: { name: '<No Location>' } } as any);
    }

    /** Display value for the date */
    public get date_string(): string {
        return this._display.date;
    }
    /** Display valuie for the start and end times of the booking */
    public get time_period(): string {
        return this._display.time;
    }
    /** Display value for the start time of the booking */
    public get start_time(): string {
        return this._display.start;
    }
    /** Display value for the end time of the booking */
    public get end_time(): string {
        return this._display.end;
    }
    /** Display value for the duration of the booking */
    public get length_string(): string {
        return this._display.duration;
    }
    /** Display value for the location of the booking */
    public get location(): string {
        return this.space_list.map((space) => space.name).join(', ') || 'No location set';
    }
    /** Display value for the level of the first space in the booking */
    public get level(): string {
        return this.space.level.name;
    }

    /**
     * Make a copy of this object
     */
    public clone(): Booking {
        return new Booking(this);
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(): Booking {
        return new Booking({
            ...this.toJSON(),
            id: null,
            email: null,
            icaluid: null,
        });
    }

    /**
     * Convert object into plain object
     */
    public toJSON(this: Booking): HashMap {
        const data = super.toJSON();
        const date = dayjs(data.date);
        data.start = data.all_day ? date.startOf('d').unix() : date.unix();
        data.end = date.add(data.duration || 60, 'm').unix();
        data.auto_approve = data.space_list.map((space) => {
            return !this.id || this.time_changed || !this.approval_status[space.email]
                ? !space ||
                      (space instanceof Space &&
                          !space.byRequest({
                              date: data.date,
                              duration: data.duration,
                              host: data.organiser,
                          }))
                : (this.approval_status[space.email] || '').indexOf('tentative') < 0;
        });
        if (data.body && !data.notes.find((note) => note.message === data.body)) {
            data.notes = data.notes.filter((note) => note.type !== 'description');
            data.notes.push({
                type: 'description',
                message: data.body,
                author: this.creator.email,
                date: dayjs().valueOf(),
            } as BookingNote);
            data.notes.sort((a, b) => b.date - a.date);
        }
        data.breakdown = data.breakdown || {};
        data.setup = data.setup || {};
        data.organiser = data.organiser.toJSON();
        data.creator = data.creator.toJSON();
        data.attendees = unique(
            [data.organiser].concat(data.attendees.map((i) => i.toJSON())),
            'email'
        );
        data.notify_users = [data.organiser.email];
        data.room_ids = data.space_list.map((space) => space.email);
        data.catering = data.catering.filter((order) => data.room_ids.includes(order.location_id));
        data.catering = mergeCateringOrders(data.catering);
        data.old_date = this.date;
        if (data.id) {
            data.from_room = this.space ? this.space.email : '';
        }
        data.description = data.body;
        data.recurr = data.recurrence;
        data.booking_type = data.class || data.booking_type;
        if (data.all_day) {
            data.all_day = date.format('YYYY-MM-DD');
        }
        delete data.class;
        delete data.body;
        delete data.type;
        delete data.recurrence;
        delete data.space_list;
        delete data.date;
        delete data.duration;
        delete data.display;
        delete data.email;
        delete data.name;
        return data;
    }
}

/**
 * Merge catering orders with same time and location
 * @param order_list List of catering orders
 */
export function mergeCateringOrders(order_list: CateringOrder[]) {
    for (let i = 0; i < order_list.length; i++) {
        const orders = order_list.filter(
            (order) =>
                order.location_id === order_list[i].location_id &&
                order.delivery_time === order_list[i].delivery_time
        );
        if (orders.length > 1) {
            const new_list = order_list.filter(
                (order) =>
                    !(order.location_id === order_list[i].location_id &&
                    order.delivery_time === order_list[i].delivery_time)
            );
            new_list.push(new CateringOrder({
                ...order_list[i],
                items: flatten(orders.map(order => order.items))
            }));
            order_list = new_list;
            i = 0;
        }
    }
    return order_list;
}
