import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { first } from 'rxjs/operators';

import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from '../../base.directive';
import { ApplicationIcon } from '../../utilities/settings.interfaces';
import { MatDialog } from '@angular/material/dialog';
import { NavigationEnd, Router } from '@angular/router';
import { generateFeedbackUrl, Urls } from '../../utilities/urls';

@Component({
    selector: 'a-topbar-header',
    template: `
        <div [class]="'topbar-header ' + page">
            <div class="block home">
                <button mat-icon-button name="home-button" class="home-button"
                        [attr.aria-label]="'Home'"
                        (click)="goToHome()">
                    <img src="assets/img/home.svg"/>
                </button>
            </div>
            <div class="block company-logo-container">
                <div class="logo" *ngIf="logo">
                    <i *ngIf="logo.type === 'icon'" [class]="logo.class">{{ logo.content }}</i>
                    <img *ngIf="logo.type === 'img'" [src]="logo.src | safe: 'resource'"/>
                </div>
            </div>
            <div class="block actions-container">
                <button mat-icon-button name="feedback-button" class="feedback-button"
                        matTooltip="Feedback"
                        [attr.aria-label]="'Feedback'"
                        (click)="goToFeedback()">
                    <img src="assets/img/chat.svg" alt="Feedback Icon"/>
                </button>
                <button mat-icon-button name="help-button" class="help-button"
                        matTooltip="Help"
                        [attr.aria-label]="'Help'"
                        (click)="goToHelp()">
                    <img src="assets/img/question.svg" alt="Help Icon"/>
                </button>
            </div>
        </div>
    `,
    styleUrls: ['./topbar-header.component.scss'],
})
export class TopbarHeaderComponent extends BaseDirective implements OnInit {
    /** Logo of the application organisation */
    public logo: ApplicationIcon;
    /** Whether to show menu */
    @Input() public menu: boolean;
    /** Name of the page being rendered */
    @Input() public page: string;
    /** Emiiter for changes to menu state */
    @Output() public menuChange = new EventEmitter<boolean>();
    /** Name of the currently active page */
    public currentPage: string;

    constructor(private _service: ApplicationService, private _dialog: MatDialog,
                private router: Router) {
        super();
    }

    public ngOnInit(): void {
        this._service.initialised.pipe(first(_ => _)).subscribe(() => {
            this.logo = this._service.setting('app.logo_dark') || { type: 'icon' };
        });
        this.timeout('Getting Title', () => this.updateRoute(document.title));
        this.router.events.subscribe((event: NavigationEnd) => {
            /* istanbul ignore else */
            if (event instanceof NavigationEnd) {
                this.timeout('Getting Title', () => this.updateRoute(document.title));
            }
        });
    }

    public updateRoute(location) {
        location = location.substr(0, location.indexOf(' |'));
        this.currentPage = location;
    }

    /** Set menu state to `true` */
    public showMenu() {
        this.menu = true;
        this.menuChange.emit(this.menu);
    }

    public async goToHome() {
        await this.router.navigate(['/']);
        this._dialog.closeAll();
    }

    public goToFeedback() {
        window.open(generateFeedbackUrl(this.currentPage));
    }

    public goToHelp() {
        window.open(Urls.Help);
    }
}
