import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import {
    MeetingDetailsOverlayComponent,
    MeetingDetailsModalData,
} from 'src/app/overlays/meeting-details-overlay/meeting-details-overlay.component';
import { UsersService } from 'src/app/services/data/users/users.service';
import { User } from 'src/app/services/data/users/user.class';

import * as dayjs from 'dayjs';
import { first } from 'rxjs/operators';

export interface BookingLike {
    /** ID of the item */
    id: string;
    /** Sorting weight of the item */
    order_id: string;
    /** Type of item to render */
    type: 'date' | 'event' | 'empty';
    /** Whether the event is on the current date */
    is_today: boolean;
    /** Title of the event */
    title: string;
    /** Date in UTC milliseconds */
    date: number;
    /** Current state of the booking */
    status?: 'future' | 'upcoming' | 'done' | 'started' | 'in_progress';

    [key: string]: any;
}

@Component({
    selector: 'schedule-event-item',
    templateUrl: './list-item.component.html',
    styleUrls: ['./list-item.component.scss'],
})
export class ScheduleEventListItemComponent extends BaseDirective {
    /** Booking to display */
    @Input() public event: BookingLike;
    /** User who's schedule is being viewed */
    @Input() public user: User;

    @Output() public viewing = new EventEmitter<boolean>();

    @Output() public deleted = new EventEmitter();

    public get in_progress(): boolean {
        return this.event.status === 'in_progress';
    }

    public get date_display(): string {
        const date = dayjs(this.event.date);
        const now = dayjs();
        return now.isSame(date, 'd') ? 'Today' : date.format('DD MMM YYYY');
    }

    /** Display location of the booking event */
    public get location(): string {
        const booking: Booking = this.event as any;
        return (booking.space_list || [booking.space]).map((i) => i.name).join(', ');
    }

    /** Image name to display for the event */
    public get type_image(): string {
        const booking: Booking = this.event as any;
        return booking.has_visitors ? 'external' : 'book_room';
    }

    public get time(): string {
        const booking: Booking = this.event as any;
        return booking.all_day || booking.duration > 23 * 60
            ? 'All Day'
            : dayjs(booking.date).format('h:mm A');
    }

    /** Status of the current booking */
    public get status(): 'approved' | 'tentative' | 'declined' | '' {
        if (this.event.type === 'date' || this.event.type === 'empty') return '';
        const event: Booking = this.event as any;
        if (event.declined) {
            return 'declined';
        } else if (event.tentative) {
            return 'tentative';
        } else if (event.approved) {
            return 'approved';
        }
        return '';
    }

    constructor(private _dialog: MatDialog, private _users: UsersService) {
        super();
    }

    public openDetailsModal() {
        /* istanbul ignore else */
        if (this.event instanceof Booking) {
            this.viewing.emit(true);
            const ref = this._dialog.open<MeetingDetailsOverlayComponent, MeetingDetailsModalData>(
                MeetingDetailsOverlayComponent,
                {
                    maxHeight: 'auto',
                    maxWidth: 'auto',
                    width: 'auto',
                    height: 'auto',
                    hasBackdrop: false,
                    closeOnNavigation: true,
                    data: {
                        booking: this.event,
                        as_delegate: this._users.current.email !== (this.user || {}).email,
                        delegate: this.user ? this.user.email : '',
                        is_first: false,
                        is_last: false,
                    },
                }
            );
            ref.componentInstance.event
                .pipe(first((_) => _.reason === 'action'))
                .subscribe(() => this.deleted.emit());
            ref.afterClosed().subscribe(() => this.viewing.emit(false));
        }
    }
}
