import { Component, Input } from '@angular/core';

import { CateringOrder } from '../../../services/data/catering/catering-order.class';
import { Space } from 'src/app/services/data/spaces/space.class';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

@Component({
    selector: 'meeting-details-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss'],
})
export class MeetingDetailsCateringComponent {
    /** List of catering orders */
    @Input() public catering: CateringOrder[];
    /** Active space to display */
    @Input() public space: Space;
    /** Active space to display */
    @Input() public booking: Booking;
    /** Whether all orders should be shown */
    public show_orders: boolean;

    constructor(private _org: OrganisationService) { }

    /** List of orders for the active space */
    public get orders(): CateringOrder[] {
        return (this.catering || []).filter((order) => order.location_id === this.space.email);
    }

    public get symbol() {
        const bld = this._org.buildings.find(bld => this.space.zones.includes(bld.id));
        return bld?.currency || '';
    }

    /** Charge code for orders in the active space */
    public get notes(): string {
        const the_note = this.booking.notes.find(
            (note) => note.type === 'catering' && note.space === this.space.email
        );
        return the_note ? the_note.message : '<No notes>';
    }

    /** Charge code for orders in the active space */
    public get charge_code(): string {
        return this.orders.length > 0
            ? this.orders[0].charge_code || '<No charge code>'
            : '<No charge code>';
    }
}
