import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ApplicationIcon } from '../../utilities/settings.interfaces';

export enum AlertMode {
    Warning = 'warning',
    Coachmark = 'coachmark'
}

@Component({
    selector: 'app-alert-banner',
    template: `
        <div class="alert-container {{mode}}">
            <div class="alert-icon-container {{mode}}">
                <i class="material-icons {{icon.class}}">{{icon.content}}</i>
            </div>
            <div class="content-wrap">
                <span name="message" class="message {{mode}}">{{message}}</span>
                <div class="actions-button-container">
                    <button name="action-button" type="button" mat-stroked-button
                            color="{{materialColor}}"
                            (click)="action.emit($event)">{{buttonText}}</button>
                </div>
            </div>
            <button mat-icon-button name="close-button" class="close-button"
                    (click)="close.emit($event)">
                <app-icon
                    [icon]="{ src: 'assets/img/close_outline.svg', type: 'img' }"
                ></app-icon>
            </button>
        </div>
    `,
    styleUrls: ['./alert-banner.component.scss'],
})
export class AlertBannerComponent implements OnInit {

    @Input()
    public mode: AlertMode;

    @Output()
    public close = new EventEmitter<MouseEvent>();

    @Output()
    public action = new EventEmitter<MouseEvent>();

    @Input()
    public message: string;

    @Input()
    public buttonText: string;

    public icon: ApplicationIcon;

    public ngOnInit(): void {
        // TODO: switch based on mode if necessary.
        this.icon = { type: 'icon', class: 'alert-icon', content: 'warning' };
    }

    public get materialColor(): string {
        switch (this.mode) {
            case AlertMode.Warning:
                return 'black';
            case AlertMode.Coachmark:
                return 'coachmark';
            default:
                return 'black';
        }
    }

}
